import { useUser } from '../auth/useUser';
import styled from 'styled-components';
import AnimationCard from '../components/AnimationCard';
import { PresentationOverview } from '../components/PresentationOverview';
import { PresentationGoogleOauth } from '../components/PresentationGoogleOauth';
import { PresentationReactJs } from '../components/PresentationReactJs';
import { PresentationGit } from '../components/PresentationGit';
import { PresentationNode } from '../components/PresentationNode';
import { PresentationAws } from '../components/PresentationAws';
import { PresentationTools } from '../components/PresentationTools';

export const PresentationPage = () => {
  const user = useUser();
  const { userName, userPicture, email } = user;

  const ScrollEffect = styled.div`
    > * {
      scroll-snap-align: start;
      padding-top: 1px;
    }
  `;

  return (
    <div className='dark-mode'>
      <ScrollEffect>
        <AnimationCard
          userName={userName}
          userPicture={userPicture}
          email={email}
        />
        <PresentationOverview />
        <PresentationReactJs />
        <PresentationNode />
        <PresentationGit />
        <PresentationGoogleOauth />
        <PresentationAws />
        <PresentationTools />
      </ScrollEffect>
    </div>
  );
};
