import React from 'react';

const toolsUsed = [
  {
    name: 'JSON Web Token',
    src: './images/jwtlogo.svg',
    desk: 'JSON Web Token',
  },
  {
    name: 'Tailwindcss',
    src: './images/tailwindcsslogo.svg',
    desk: 'Tailwindcss',
  },
  {
    name: 'Styled-Components',
    src: './images/styledcomponents.png',
    desk: 'Styled-Components',
  },
  {
    name: 'React Spring',
    src: './images/reactspring.svg',
    desk: 'React Spring',
  },
];

export const PresentationTools = () => {
  return (
    <div className='dark-mode h-screen'>
      <div className='flex flex-row justify-evenly flex-wrap mt-14 sm:mt-24'>
        {toolsUsed.map((item) => (
          <div className='flex flex-row sm:flex-col justify-between items-center text-center space-y-1 sm:space-y-4'>
            <div className='flex justify-center bg-white h-20 md:h-32 lg:h-40 xl:h-48 w-20 md:w-32 lg:w-40 xl:w-48'>
              <img
                className='self-center w-60'
                src={item.src}
                alt={item.name}
              />
            </div>
            <div className='flex flex-col justify-center w-60 space-y-1 sm:space-y-2'>
              <h1 className='h1-pres'>{item.name}</h1>
              <p className='p-pres'>{item.desk}</p>
            </div>
          </div>
        ))}
      </div>
      <div className='flex flex-col justify-center h-60 mt-1 md:mt-12'>
        <div className='center-second-div space-y-2 sm:space-y-8'>
          <h1 className='h1-pres'>Used Tools</h1>
          <p className='p-pres'>
            JWT or JSON Web Token is used to generate tokens for user
            authentication.
            <br />
            Tailwindcss for classes styling.
            <br />
            Styled-Components to separated concerns when styling with states.
            <br />
            React Spring is used for view animation on this website.
          </p>
        </div>
      </div>
    </div>
  );
};
