import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useToken } from '../auth/useToken';
import { useQueryParams } from '../util/useQueryParams';

const LoginPage = () => {
  const [, setToken] = useToken();

  const [errorMessage, setErrorMessage] = useState('');
  const [emailValue, setEmailValue] = useState('');
  const [passwordValue, setPasswordValue] = useState('');

  const [googleOauthUrl, setGoogleOauthUrl] = useState('');
  const { token: oauthToken } = useQueryParams();

  const history = useHistory();

  const checkEmailValue = () => {
    // don't remember from where i copied this code, but this works.
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (re.test(emailValue)) {
      // this is a valid email address
      // call setState({email: email}) to update the email
      // or update the data in redux store.
      return true;
    } else {
      // invalid email, maybe show an error to the user.
      return false;
    }
  };

  useEffect(() => {
    if (oauthToken) {
      setToken(oauthToken);
      history.push('/presentation');
      window.location.reload();
    }
  }, [oauthToken, setToken, history]);

  useEffect(() => {
    const loadOauthUrl = async () => {
      try {
        const response = await axios.get('/auth/google/url');
        const { url } = response.data;
        setGoogleOauthUrl(url);
      } catch (e) {
        console.log(e);
      }
    };
    loadOauthUrl();
  }, []);

  const onLogInClicked = async () => {
    try {
      const response = await axios.post('api/login', {
        email: emailValue,
        password: passwordValue,
      });
      const { token } = response.data;
      setToken(token);
      history.push('/presentation');
      window.location.reload();
    } catch (e) {
      setErrorMessage('Email or password wrong');
    }
  };

  return (
    <div className='dark-mode'>
      <div className='flex flex-col justify-center h-screen'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Log In</h1>
          {errorMessage && (
            <div className='p-text text-red-500'>{errorMessage}</div>
          )}
          <input
            value={emailValue}
            onChange={(e) => {
              setEmailValue(e.target.value);
            }}
            type='text'
            className='input-text'
            placeholder='someone@gmail.com'
          />
          <input
            value={passwordValue}
            onChange={(e) => {
              setPasswordValue(e.target.value);
            }}
            className='input-text'
            type='password'
            placeholder='password'
          />
          <button
            disabled={!emailValue || !passwordValue || !checkEmailValue()}
            onClick={onLogInClicked}
            className='btn-sort'
          >
            Log In
          </button>
          <div className='flex flex-col self-start space-y-2'>
            <button
              onClick={() => history.push('/forgot-password')}
              className='btn-text'
            >
              Forgot your password?
            </button>
            <button
              onClick={() => history.push('/signup')}
              className='btn-text'
            >
              Dont't have an account?
              <span className='hover:text-red-500'>Sign Up</span>
            </button>
          </div>
          <div className='flex flex-row self-start w-1/2 h-12 items-center'>
            <img
              className='w-6 h-6 mr-4'
              src='./images/googlelogo.svg'
              alt='Google Logo'
            />
            <button
              type='reset'
              disabled={!googleOauthUrl}
              onClick={() => {
                window.location.href = googleOauthUrl;
              }}
              className='btn-long'
            >
              Log in with Google
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
