import { useHistory } from 'react-router-dom';

export const EmailVerificationFail = () => {
  const history = useHistory();

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Uh oh...</h1>
          <p className='p-text'>
            Something went wrong while trying to verify your email.
          </p>
          <button className='btn-long' onClick={() => history.push('/singup')}>
            Back to Sign-up
          </button>
        </div>
      </div>
    </div>
  );
};
