import axios from 'axios';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useToken } from '../auth/useToken';

export const SignUpPage = () => {
  const [, setToken] = useToken();

  const [errorMessage, setErrorMessage] = useState('');
  const [emailValue, setEmailValue] = useState('');
  const [passwordValue, setPasswordValue] = useState('');
  const [confirmPasswordValue, setConfirmPasswordValue] = useState('');

  const checkEmailValue = () => {
    // don't remember from where i copied this code, but this works.
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (re.test(emailValue)) {
      // this is a valid email address
      // call setState({email: email}) to update the email
      // or update the data in redux store.
      return true;
    } else {
      // invalid email, maybe show an error to the user.
      return false;
    }
  };

  const onSignUpClicked = async () => {
    try {
      const response = await axios.post('/api/signup', {
        email: emailValue,
        password: passwordValue,
      });
      const { token } = response.data;
      setToken(token);
      history.push('/please-verify');
    } catch (e) {
      if (e.response.status === 409)
        setErrorMessage('E-mail already registered');
      else setErrorMessage('Email tidak valid');
    }
  };

  const history = useHistory();
  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Sign Up</h1>
          {errorMessage && (
            <div className='fail text-lg text-red-400'>{errorMessage}</div>
          )}
          <input
            value={emailValue}
            onChange={(e) => {
              setEmailValue(e.target.value);
            }}
            type='text'
            className='input-text'
            placeholder='someone@gmail.com'
          />
          <input
            value={passwordValue}
            onChange={(e) => {
              setPasswordValue(e.target.value);
            }}
            className='input-text'
            type='password'
            placeholder='password'
          />
          <input
            value={confirmPasswordValue}
            onChange={(e) => {
              setConfirmPasswordValue(e.target.value);
            }}
            className='input-text'
            type='password'
            placeholder='confirm password'
          />
          <button
            disabled={
              !emailValue ||
              !checkEmailValue() ||
              !passwordValue ||
              passwordValue !== confirmPasswordValue
            }
            onClick={onSignUpClicked}
            className='btn-sort'
          >
            Sign Up
          </button>
          <button onClick={() => history.push('/login')} className='btn-text'>
            Already have an account?
            <span className='hover:text-red-500'> Log in</span>
          </button>
        </div>
      </div>
    </div>
  );
};
