import React from 'react';

export const PresentationAws = () => {
  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div space-y-8'>
          <img className='w-96' src='./images/awslogo.svg' alt='AWS Logo' />
          <h1 className='h1-pres'>AWS - EC2 and SES</h1>
          <p className='p-pres'>
            Amazon Web Service EC2 instance for web server and SES or Simple
            Email Server for mail server.
            <br />
            If you Log In without Google Oauth, you will be asked to verify
            email by clicking the link that sends to your email.
          </p>
        </div>
      </div>
    </div>
  );
};
