import axios from 'axios';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';

export const ForgotPasswordPage = () => {
  const [errorMessage, setErrorMessage] = useState('');
  const [emailValue, setEmailValue] = useState('');
  const [success, setSuccess] = useState(false);
  const history = useHistory();

  const checkEmailValue = () => {
    // don't remember from where i copied this code, but this works.
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (re.test(emailValue)) {
      // this is a valid email address
      // call setState({email: email}) to update the email
      // or update the data in redux store.
      return true;
    } else {
      // invalid email, maybe show an error to the user.
      return false;
    }
  };

  const onSubmitClicked = async () => {
    try {
      await axios.put(`/api/forgot-password/${emailValue}`);
      setSuccess(true);
      setTimeout(() => {
        history.push('/login');
      }, 3000);
    } catch (e) {
      setErrorMessage('Email not found');
    }
  };

  return success ? (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Success</h1>
          <p className='p-text'>Check your email for a reset link</p>
        </div>
      </div>
    </div>
  ) : (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Forgot password</h1>
          <p className='p-text'>
            Enter your email and we will send email to a reset link
          </p>
          {errorMessage && (
            <div className='p-text text-red-500'>{errorMessage}</div>
          )}
          <input
            value={emailValue}
            onChange={(e) => {
              setEmailValue(e.target.value);
            }}
            type='text'
            className='input-text'
            placeholder='someone@gmail.com'
          />
          <button
            disabled={!emailValue || !checkEmailValue()}
            onClick={onSubmitClicked}
            className='btn-long'
          >
            Send Reset Link
          </button>
        </div>
      </div>
    </div>
  );
};
