import { useHistory } from 'react-router-dom';

export const PasswordResetSuccess = () => {
  const history = useHistory();

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Success!</h1>
          <p className='p-text'>
            Your password has been reset, now please login with your new
            password
          </p>
          <button className='btn-sort' onClick={() => history.push('/login')}>
            Log In
          </button>
        </div>
      </div>
    </div>
  );
};
