import { useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { PasswordResetSuccess } from './PasswordResetSuccess';
import { passwordResetFail } from './PasswordResetFail';

export const PasswordResetLandingPage = () => {
  const [isSuccess, setIsSuccess] = useState(false);
  const [isFailure, setIsFailure] = useState(false);
  const [passwordValue, setPasswordValue] = useState('');
  const [confirmPasswordValue, setConfirmPasswordValue] = useState('');
  const { passwordResetCode } = useParams();

  const onResetClicked = async () => {
    try {
      await axios.put(`/api/users/${passwordResetCode}/reset-password`, {
        newPassword: passwordValue,
      });
      setIsSuccess(true);
    } catch (e) {
      setIsFailure(true);
    }
  };

  if (isFailure) return <passwordResetFail />;
  if (isSuccess) return <PasswordResetSuccess />;

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Reset Password</h1>
          <p className='p-text'>Please enter a new password</p>
          <input
            className='input-text'
            type='password'
            value={passwordValue}
            onChange={(e) => setPasswordValue(e.target.value)}
            placeholder='password'
          />
          <input
            className='input-text'
            type='password'
            value={confirmPasswordValue}
            onChange={(e) => setConfirmPasswordValue(e.target.value)}
            placeholder='confirm password'
          />
          <button
            className='btn-long'
            disabled={
              !passwordValue ||
              !confirmPasswordValue ||
              passwordValue !== confirmPasswordValue
            }
            onClick={onResetClicked}
          >
            Reset Password
          </button>
        </div>
      </div>
    </div>
  );
};
