import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

export const PleaseVerifyEmailPage = () => {
  const history = useHistory();

  useEffect(() => {
    setTimeout(() => {
      history.push('/');
    }, 3000);
  }, [history]);

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Thaks for Signing Up!</h1>
          <p className='p-text'>
            A verification email has been sent to the email address you
            provided. Please verify your email to unlock full site features.
          </p>
        </div>
      </div>
    </div>
  );
};
