import { useHistory } from 'react-router-dom';

export const EmailVerificationSuccess = () => {
  const history = useHistory();

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Success!</h1>
          <p className='p-text'>
            Thanks for verifying your email, now you can use all the app's
            features.
          </p>
          <button className='btn-long' onClick={() => history.push('/')}>
            Go to home page
          </button>
        </div>
      </div>
    </div>
  );
};
