import React from 'react';

export const PresentationGoogleOauth = () => {
  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div space-y-8'>
          <img
            className='w-20 h-20 md:w-40 md:h-40'
            src='./images/googlelogo.svg'
            alt='Google Logo'
          />
          <h1 className='h1-pres'>Google Oauth2</h1>
          <p className='p-pres'>
            You can log in with your's Gmail. Its more secure, no need to pass
            the password.
          </p>
        </div>
      </div>
    </div>
  );
};
