import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

export const PleaseLoginPage = () => {
  const history = useHistory();

  useEffect(() => {
    setTimeout(() => {
      history.push('/login');
    }, 3000);
  }, [history]);

  return (
    <div className='dark-mode'>
      <div className='center-first-div'>
        <div className='center-second-div box-style'>
          <h1 className='h1-text'>Please Login</h1>
          <p className='p-text'>Login first before continue to presentation.</p>
        </div>
      </div>
    </div>
  );
};
