import { useRef, useState } from 'react';
import { useSpring, animated } from '@react-spring/web';

const calc = (x, y, rect) => [
  -(y - rect.top - rect.height / 2) / 5,
  (x - rect.left - rect.width / 2) / 5,
  1,
];
const trans = (x, y, s) =>
  `perspective(600px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`;

export default function Card({ userName, userPicture, email }) {
  const ref = useRef(null);
  const [xys, set] = useState([0, 0, 1]);
  const props = useSpring({
    xys,
    config: {
      mass: 1,
      tension: 150,
      friction: 80,
      clamp: true,
      precision: 0.01,
      velocity: 0,
      easing: (t) => t,
    },
  });

  return (
    <div className='flex justify-center h-screen'>
      <div className='h-full w-full overflow-hidden'>
        <div className='ccard-main' ref={ref}>
          <animated.div
            className='ccard w-150 h-96 border-2 shadow-xl'
            style={{ transform: props.xys.to(trans) }}
            onMouseLeave={() => set([0, 0, 1])}
            onMouseMove={(e) => {
              const rect = ref.current.getBoundingClientRect();
              set(calc(e.clientX, e.clientY, rect));
            }}
          >
            {userName ? (
              <div className='flex flex-col h-full'>
                <div className='flex flex-col text-center items-center'>
                  <h1 className='text-5xl pt-16'>Welcome</h1>
                  <div className='flex flex-row pt-20 items-center'>
                    <div className='flex flex-col mr-8'>
                      <p className='text-xl self-start'>Name: {userName}</p>
                      <p className='text-xl self-start'>E-mail: {email}</p>
                    </div>
                    <img
                      className='flex-grow-0 self-end rounded-lg'
                      src={userPicture}
                      alt={userName}
                    />
                  </div>
                </div>
              </div>
            ) : (
              <div className='flex flex-col h-full'>
                <div className='flex flex-col text-center items-center flex-shrink-0'>
                  <h1 className='text-5xl pt-20'>Welcome</h1>
                  <p className='text-3xl pt-20'>{email}</p>
                </div>
              </div>
            )}
          </animated.div>
        </div>
      </div>
    </div>
  );
}
