import { NavBar } from './NavBar';
import { BrowserRouter as Router } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faToggleOn, faToggleOff } from '@fortawesome/free-solid-svg-icons';
import { Switchs } from './Switchs';
import { useDark } from './components/useDark';

export const App = () => {
  const [darkMode, setDarkMode] = useDark();
  const toggleDarkClick = () => {
    if (darkMode === 'dark') {
      setDarkMode('light');
    } else {
      setDarkMode('dark');
    }
  };

  const toggle = () => {
    return (
      <>
        {darkMode === 'dark' ? (
          <FontAwesomeIcon
            onClick={toggleDarkClick}
            className='dark-off text-3xl absolute right-0 mr-10 hover:text-gray-300'
            icon={faToggleOn}
          />
        ) : (
          <FontAwesomeIcon
            onClick={toggleDarkClick}
            className='dark-on text-3xl absolute right-0 mr-10 hover:text-gray-600'
            icon={faToggleOff}
          />
        )}
      </>
    );
  };

  return (
    <Router>
      <div className={`App font-sans ${darkMode}`}>
        <NavBar darkToggleMode={toggle()} />
        <Switchs />
      </div>
    </Router>
  );
};
