import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBug } from '@fortawesome/free-solid-svg-icons';

export const Footer = () => {
  return (
    <footer
      id='contact'
      className='text-white bg-black dark:bg-white dark:text-gray-500 h-60'
    >
      <div className='flex flex-col justify-center h-full space-y-6 md:space-y-12 mx-4'>
        <div className='flex pt-4 justify-self-start space-x-4'>
          <h1 className='text-lg font-light'>Contact :</h1>
          <div className='flex flex-col space-y-2'>
            <div className='flex flex-row'>
              <a
                className='flex flex-row space-x-4'
                href='https://wa.me/6289611551774'
              >
                <img
                  className='w-8 h-8'
                  src='./images/whatsappgreen.svg'
                  alt='whatsapp'
                />
                <p className='text-lg font-light'>0896-1155-1774</p>
              </a>
            </div>
            <div className='flex flex-row'>
              <div className='flex flex-row space-x-4'>
                <img className='w-8 h-8' src='./images/gmail.svg' alt='gmail' />
                <p className='text-lg font-light'>rindraakbars@gmail.com</p>
              </div>
            </div>
          </div>
        </div>
        <div className='flex flex-col items-center pb-4 space-y-4'>
          <FontAwesomeIcon className='text-5xl' icon={faBug} />
          <p className='text-lg text-center'>
            Copyright &copy; 2021-present Raden Indra Akbar
          </p>
        </div>
      </div>
    </footer>
  );
};
