import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import styled, { keyframes } from 'styled-components';

const clear = keyframes`
  to {
    opacity: 1;
    transform: none;
  }
`;

const slideDown = keyframes`
  from {
    transform: translateY(-2rem);
    opacity: 0;
  }
  to {
    transform: none;
    opacity: 1;
  }
`;

const pulse = keyframes`
  0% {
    opacity: 1;
    transform: none;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
`;

const HeaderCueIcon = styled(FontAwesomeIcon)`
  animation: ${slideDown} 0.5s 1.8s forwards,
    ${pulse} 2s 4s ease-in-out infinite;
  transform: translateY(-2rem);
`;

export const Hero = () => {
  const [scrollPosition, setScrollPosition] = useState(0);
  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const Hidden = () => (scrollPosition > 0 ? 'none' : 'block');

  const HeaderCue = styled.div`
    animation: ${clear} 0.5s 1.5s forwards;
    transform: translateY(2rem);
    display: ${Hidden()};
  `;

  return (
    <header className='relative site-header overflow-hidden h-screen border-b-2'>
      <div className='flex flex-col h-screen'>
        <div className='flex flex-col flex-grow justify-center'>
          <div className='flex flex-col items-center text-center container justify-center mx-auto w-3/4 space-y-16'>
            <h1 className='h1-pres font-semibold'>
              Hello, my name is Raden Indra Akbar Saputra and I'm looking for a
              new job.
            </h1>
            <p className='p-pres font-medium'>
              Welcome to my website.
              <br />
              Feel free to explore this website, and find out about me.
            </p>
          </div>
        </div>
        <HeaderCue className='header-cue border-t-2 absolute inset-x-0 bottom-0 text-center overflow-hidden'>
          <HeaderCueIcon
            className='text-red-500 opacity-1'
            icon={faChevronDown}
          />
        </HeaderCue>
      </div>
    </header>
  );
};
