import { Route, Switch } from 'react-router-dom';
import { EmailVerificationLandingPage } from './pages/EmailVerificationLandingPage';
import { HomePage } from './pages/HomePage';
import LoginPage from './pages/LoginPage';
import { PleaseVerifyEmailPage } from './pages/PleaseVerifyEmailPage';
import { SignUpPage } from './pages/SignUpPage';
import { ForgotPasswordPage } from './pages/ForgotPasswordPage';
import { PrivateRoute } from './auth/PrivateRoute';
import { PasswordResetLandingPage } from './pages/PasswordResetLandingPage';
import { LogOutSuccess } from './pages/LogOutSuccess';
import { PresentationPage } from './pages/PresentationPage';
import { PleaseLoginPage } from './pages/PleaseLoginPage';
import { PrivateRouteNoUser } from './auth/PrivateRouteNoUser';
import { AboutPage } from './pages/AboutPage';

export const Switchs = () => {
  return (
    <Switch>
      <Route path='/' exact>
        <HomePage />
      </Route>
      <Route path='/verify-email/:verificationString'>
        <EmailVerificationLandingPage />
      </Route>
      <Route path='/forgot-password'>
        <ForgotPasswordPage />
      </Route>
      <PrivateRoute path='/login'>
        <LoginPage />
      </PrivateRoute>
      <Route path='/reset-password/:passwordResetCode'>
        <PasswordResetLandingPage />
      </Route>
      <Route path='/please-verify'>
        <PleaseVerifyEmailPage />
      </Route>
      <PrivateRoute path='/signup'>
        <SignUpPage />
      </PrivateRoute>
      <Route path='/logout-success'>
        <LogOutSuccess />
      </Route>
      <PrivateRouteNoUser path='/presentation'>
        <PresentationPage />
      </PrivateRouteNoUser>
      <Route path='/please-login'>
        <PleaseLoginPage />
      </Route>
      <Route path='/about'>
        <AboutPage />
      </Route>
    </Switch>
  );
};
