import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBug, faBars } from '@fortawesome/free-solid-svg-icons';
import styled, { keyframes } from 'styled-components';
import { useUser } from './auth/useUser';
import { useHistory } from 'react-router-dom';

const navigation = [
  { name: 'Home', href: '/' },
  { name: 'Presentation', href: '/presentation' },
  { name: 'About', href: '/about' },
];

const NavLink = styled.a`
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    max-width: 100px;
    height: 21px;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
    transition: all 0.5s cubic-bezier(0.03, 0.78, 0.4, 1.39);
    opacity: 0;
  }
  &:hover {
    &::before {
      background-color: rgba(0, 0, 0, 0.2);
      transform: scaleX(1);
      opacity: 1;
    }
  }
`;

export const NavBar = ({ darkToggleMode }) => {
  const user = useUser();
  const clear = keyframes`
     to {
    opacity: 1;
    transform: none;}
    `;

  const NavToggle = styled.div`
    animation: ${clear} 0.3s forwards;
    transform: translateY(3rem);
  `;

  const [toggleBtn, setToggleBtn] = useState(false);

  const history = useHistory();

  const logOut = () => {
    localStorage.removeItem('token');
    history.push('/logout-success');
    window.location.reload();
  };

  const toggleBtnClick = () => {
    setToggleBtn(!toggleBtn);
  };

  const checkUser = () => {
    if (!user) {
      return (
        <NavLink className='relative mb-2' href='/login' key='Log In'>
          Log In
        </NavLink>
      );
    } else {
      return (
        <NavLink
          onClick={logOut}
          className='relative mb-2 cursor-pointer'
          key='Log In'
        >
          Log Out
        </NavLink>
      );
    }
  };

  return (
    <>
      <nav
        className='fixed top-0 z-10 uppercase border-b-2 
      text-gray-500 bg-white dark:bg-black dark:text-white w-screen'
      >
        <div className='flex flex-row p-2 items-center'>
          <div className='flex flex-row flex-initial items-center'>
            <a className='mx-2' href='/'>
              <FontAwesomeIcon className='text-2xl mt-1' icon={faBug} />
            </a>
            <p className='font-semibold text-sm sm:text-md md:text-lg lg:text-xl xl:text-2xl'>
              Indra Akbar
            </p>
          </div>
          <div className='flex-auto'>
            <div className='relative flex flex-row justify-end items-center'>
              <p className='mr-20 text-sm sm:text-md md:text-lg lg:text-xl'>
                Dark Mode:
              </p>
              {darkToggleMode}
            </div>
          </div>
          <div className='flex flex-row flex-initial items-center p-2 mr-2'>
            <button
              onClick={toggleBtnClick}
              className='rounded-sm md:hidden focus:ring-2 ring-gray-500 px-2'
            >
              <FontAwesomeIcon
                className='toggle-btn mt-1 right-6 text-2xl '
                icon={faBars}
              />
            </button>
            <div className='flex-col hidden md:block space-x-3'>
              {navigation.map((item) => (
                <NavLink className='relative' href={item.href} key={item.name}>
                  {item.name}
                </NavLink>
              ))}
              {checkUser()}
            </div>
          </div>
        </div>
        {toggleBtn ? (
          <NavToggle
            className='opacity-0 md:hidden absolute 
          bg-white dark:bg-black right-0 w-screen border-b-2'
          >
            <div className='flex flex-col items-end mr-8 space-y-2'>
              {navigation.map((item) => (
                <NavLink className='relative' href={item.href} key={item.name}>
                  {item.name}
                </NavLink>
              ))}
              {checkUser()}
            </div>
          </NavToggle>
        ) : null}
      </nav>
    </>
  );
};
